@import url('https://fonts.googleapis.com/css2?family=Share+Tech&display=swap');
:root {
    --main-bg-color: #fff;
    --main-theme-color: #e24493;
}
body {
    background: var(--main-bg-color);
    font-family: 'Share Tech', sans-serif;
    margin: 0;
    padding: 0;
}
.video,.cardImg{
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* object-fit: fill; */
    /* border: 1px solid black; */
}
.videoContainer{
    width: 100% !important;
    height: 100% !important;
}
.playpause {
    background-image: url('./Images/playBtn.svg');
    background-repeat: no-repeat;
    width: 22%;
    height: 22%;
    position: absolute;
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    margin: auto;
    background-size: contain;
    background-position: center;
    z-index: 9;
}
.playpause {
    opacity: 1;
    transition: opacity 400ms;
}
.playpause[hidden] {
    opacity: 0;
    display: block;
}
/* navbar */
.navbar-sticky {
    position: sticky !important;
    top: 0px;
    z-index: 99999;
    background-color: rgba(255, 255, 255, 0.5) !important;
    backdrop-filter: blur(8px);
    transition: 0.5s;
}
.navbar.bg-light {
    background-color: #E4F4F9 !important;
}
.themeBtn{
    background: #e24493;
    background: linear-gradient(206deg, #e24493 40%, #774d90 60%);
    background: -webkit-linear-gradient(206deg, #e24493 40%, #774d90 60%);
    background: -moz-linear-gradient(206deg, #e24493 40%, #774d90 60%);
    color: white;
}
.numCon {
    padding: 10px;
    border-radius: 5px;
    font-size: 20px;
}
.detailBtn{
    padding: 5px;
    border-radius: 3px;
    font-size: 14px;
    text-align: center;
}
.numberHeader {
    color: white !important;
    font-size: 20px !important;
    text-decoration: none !important;
    margin: 0 !important;
}
/* main slider */
.mainSlider{
    height: 85vh;
}
.swiper {
    width: 100%;
    height: 100%;
    background: #E4F4F9;
}
.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #E4F4F9;
    margin-right: 0 !important;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}
.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: fill;
    /* object-fit: cover; */
}
/* main Tabs */
.HeaderBookingText{
    color: white !important;
}
.MainOffersTab > .tab-content{
    width: 100%;
}
.MainOffersTab > .nav-tabs > .nav-item > button:focus{
  outline: none;
}
.offerCardCon {
    background: #e24493;
    background: linear-gradient(206deg, #e24493 40%, #774d90 60%);
    background: -webkit-linear-gradient(206deg, #e24493 40%, #774d90 60%);
    background: -moz-linear-gradient(206deg, #e24493 40%, #774d90 60%);
}
.offerCardContent {
    display: flex;
    justify-content: space-between;
    /* align-items: center; */
    height: 70px;
    overflow: hidden;
    margin-bottom: 10px;
}
.priceofferCard {
    /* color: #e24493; */
    color: black;
}
.priceCon{
    background-color: #e24493;
    border-radius: 50%;
    display: flex;
    align-items: center;
    padding: 10px;
    margin-top: 5px !important;
    color: #fff;
}
.offerCard {
    /* background: #E4F4F9; */
    background-color: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(8px);
    padding: 10px;
    border-radius: 10px;
}
/* main  */
.mainApp{
   background-color: var(--main-theme-color);
}
.sectionHeading{
    background-color: rgba(255, 255, 255, 0.5) !important;
    backdrop-filter: blur(8px);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    padding: 10px;
}
.cardVideo {
   height: 200px !important;
}
.cardTitle{
    font-size: 20px;
}
.cardVideo > .videoContainer > video ,.cardImg{
    border-radius: 10px;
    /* border: 1px solid black; */
}
.detailVCon{
    height: 50vh;
    border-bottom: 1px solid black;
}
.detailPage{
    min-height: 83vh;
}
/* Footer */
.footer{
    border-top: 1px solid white;
    height: 25vh;
    width: 100%;
}
.socialIconCon{
    display: flex;
    justify-content: center;
    align-items: center;
}
.socialIconCon > a>i{
    font-size: 50px;
    color: white;
    padding: 10px;
    cursor: pointer;
}
/* end Footer */